<template>
    <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h3>        Driver List      </h3>
            </div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Driver List</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->

<div class="col-md-12">
<div class="card">
              <!-- /.card-header -->
              <div class="card-body p-0">
                <v-client-table 
:data="tableData"
:columns="columns" 
:options="options">
</v-client-table>
              </div>
              <!-- /.card-body -->
            </div>
</div>
</div>
</div>

</template>

<script>

export default {
  name: 'DriverListvw',
  data () {
    return {items: [],
      search: '',
      listDriver: [],
      data: {},
      drivermobno: '',
      columns: ['drivertype', 'driver_id', 'driver_name', 'driver_address', 'driver_phone', 'vehicle_number', 'status', 'rating'],
      tableData: [],
      options: {
        headings: {
          drivertype: 'Driver Type',
          driver_id: 'Driver ID',
          driver_name: 'Driver Name',
          driver_address: 'Driver Address',
          driver_phone: 'Driver Mobile',
          vehicle_number: 'VehicleNo',
          status: 'Status',
          rating: 'Rating'
        },
        sortable: ['drivertype', 'driver_name', 'driver_phone', 'vehicle_number', 'status', 'rating'],
        filterable: ['drivertype', 'driver_name', 'driver_phone', 'vehicle_number', 'status', 'driver_address', 'driver_id']
      }
    }
  },
  computed:
  {
    filteredCustomers: function () {
      var self = this
      return this.items.filter(function (cust) { return cust.drivertype.indexOf(self.search) >= 0 || cust.driver_id.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 || cust.driver_name.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 || cust.driver_address.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 || cust.driver_phone.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 || cust.vehicle_number.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 || cust.status.indexOf(self.search) >= 0 })
       // return this.customers;
    }
  },
  created: function () {
    this.FetchItem()
  },
  methods: { FetchItem () {
    // console.log();
    let uri1 = this.$hostName + '/api/driver/viewDriver'
    this.axios.get(uri1).then((response) => {
      this.items = response.data
      this.tableData = response.data
    })
  }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



